.contain {
    margin:10%;
    padding-left: 15%;
    font-size: 15px;
    display: grid;
    justify-content: center;
}

ul.contain li.telephone {
    list-style-image: url('images/telephone.svg');
    padding-bottom: 20px;
}
    
ul.contain li.email {
    list-style-image: url('images/email.svg');
    padding-bottom: 20px;
}
    
ul.conatin li.github {
    list-style-image: url('images/githubC.svg');
    padding-bottom: 20px;
}

ul.conatin li.linkedin {
    list-style-image: url('images/linkedin.svg');
    padding-bottom: 20px;
}

@media only screen and (min-width: 46.875em) {
    #contact .contain {
        margin: 5%; 
        font-size: 25px;
    }
}

